import React, {Component} from 'react'
import '../../../App.css'

const weWork = 'https://www.wework.com/buildings/bourke-street--melbourne'

class AppMobile extends Component {
  render () {
    return (
      <div className="mobile-content goldenrod cc">
        <h3><i>This website is far funkier on a desktop.</i></h3>
        <br />
        <p>Flex Dapps is blockchain development studio based out of <a href={weWork} target='blank'><u>WeWork Bourke Street</u></a> in the Melbourne CBD.</p>
        <br />
        <p>We specialize in building decentralized and mixed applications using the Ethereum blockchain and other technologies.</p>
        <br />
        <p><b>Direct e-mail enquiries to hello@flexdapps.com</b><br />&copy; Flex Dapps</p>
      </div>)
  }
}

export default AppMobile



