import React, {Component} from 'react'
import PropTypes from 'prop-types'

import { Background, Foreground, Header, HeaderAnimated, AppMain, Desktop } from '../'
import { Window } from '../../elements'

import Media from 'react-media'

class Main extends Component {
  constructor (props) {
    super(props)
    this.state = {
      AppMain: true
    }
    this.togglePanel = this.togglePanel.bind(this)
  }

  togglePanel () {
    this.setState(prevState => ({
      AppMain: !prevState
    }))
  }

  eventLogger (e, data) {
    console.log('Event: ', e)
    console.log('Data: ', data)
  }

  render () {
    return (
      <div className="App">
        <Background />
        <Header/>
        <AppMain {...this.props}/>
        <Foreground {...this.props}/>
      </div>
    )
  }
}

// Main.propTypes = {
//   history: PropTypes.object.required
// }

export default Main

