
import { imports } from '../data/imports'
const { sprites } = imports

function roll (p) {
  return Math.round(Math.random() * p)
}

function rollForVH () {
  return roll(100)
}

function rollForDirection () {
  let y = roll(1)
  return y === 1 ? 'reverse' : ''
}

function rollForSrc (object) {
  const arr = Object.keys(object)
  return arr[Math.floor(Math.random() * arr.length)]
}

export function getNewSprite () {
  return {
    Name: 'Robert',
    VH: rollForVH(),
    Direction: rollForDirection(),
    Src: rollForSrc(sprites)
  }
}



