const defaultState = {
  list: []
}

function sprites (state = defaultState, action) {
  switch (action.type) {
    case 'CREATE_SPRITE':
      return {
        ...state,
        list: state.list.concat([action.payload.sprite])
      }

    case 'KILL_SPRITE':
      let i = action.index
      return {
        ...state,
        list: state.list.slice(0, i).concat(state.list.slice(i + 1))
      }
    default:
      return state
  }
}

export default sprites



