// increment sprites
import { getNewSprite } from '../services/spriteService'

export function increment (index) {
  return {
    type: 'INCREMENT',
    index
  }
}

//
export function killSprite (index) {
  return {
    type: 'KILL_SPRITE',
    index
  }
}

export function createSprite (newSprite) {
  return {
    type: 'CREATE_SPRITE',
    payload: {
      sprite: newSprite
    }
  }
}

export function updatePointer (index) {
  return {
    type: 'UPDATE_POINTER',
    index
  }
}



