import React, {Component} from 'react'
import setBinding from 'react-keydown'
import {NavLink} from 'react-router-dom'
import './appMain.css'
import SwitchButton from './switchbutton'

class Switch extends Component {
  constructor (props) {
    super(props)
    this.state = {
      buttons: [
        {
          content: <a>About<u>&nbsp;F</u>lex</a>,
          route: '/'
        },
        {
          content: <a>Our&nbsp;<u>W</u>ork</a>,
          route: '/ourwork'
        },
        {
          content: <a><u>C</u>ontact&nbsp;Us</a>,
          route: '/contact'
        },
        {
          content: <a>Close</a>,
          route: '/'
        }
      ]
    }
  }

  // _handleKeyDown (event) {
  //   let F = 70
  //   let W = 87
  //   let C = 67
  //   switch (event.keyCode) {
  //     case F:
  //       this.changeRoute('/')
  //       break
  //     case W:
  //       this.changeRoute('/ourwork')
  //       break
  //     case C:
  //       this.changeRoute('contact')
  //       break
  //     default:
  //       break
  //   }
  // }

  // componentWillMount () {
  //   document.addEventListener('keydown', this._handleKeyDown.bind(this))
  // }

  // componentWillUnmount () {
  //   document.removeEventListener('keydown', this._handleKeyDown.bind(this))
  // }

  render () {
    return (<div className="switch cc">
      {
        this.state.buttons.map((button, i) => {
          return (
            <SwitchButton {...this.props} route={button.route} key={i}i={i}>{button.content}</SwitchButton>
          )
        })
      }
    </div>)
  }
}

export default Switch



