import React, {Component} from 'react'
import PropTypes from 'prop-types'
import './appMain.css'
import { imports } from '../../../data/imports'
// import { WorkItem } from

const { Computer, Link, Didyouknow } = imports.appMain

const goToLink = () => {
  window.open('https://hodl.canya.com', '_blank')
}

const OurWork = () => {
  return (
    <div className="child cc">
      <div>
        <button className="work-item" onClick={() => window.open('https://tokenbooks.io', '_blank')}>
          <p><strong>Tokenbooks</strong></p>
          <p>Simplifying crypto accounting so you can focus on the important stuff.</p>
          <p className='mono'>https://tokenbooks.io</p>
        </button>
        <button className="work-item" onClick={() => window.open('https://hodl.canya.com', '_blank')}>
          <p><strong>Canya HODL Club</strong></p>
          <p>A platform for Canya to KYC, audit and incentivise CAN token holders.</p>
          <p className='mono'>https://hodl.canya.com</p>
        </button>
        <button className="work-item" onClick={() => window.open('http://cansend.io/', '_blank')}>
          <p><strong>CanSend.io</strong></p>
          <p>Send Ether or ERC20s to multiple people in a single transaction.</p>
          <p className='mono'>http://cansend.io/</p>
        </button>
      </div>
    </div>
  )
}

const weWork = 'https://www.wework.com/buildings/bourke-street--melbourne'

const Contact = () => {
  return (
    <div className="child cc">
      <div>
        <p>Flex Dapps is blockchain development studio based out of <a href={weWork} target='blank'><u>WeWork</u></a> (Bourke) in Melbourne, Australia.</p>
        <br />
        <p><i>We specialize in building decentralized applications using the Ethereum blockchain, POCs and mixed applications that solve problems for companies in the blockchain community.</i></p>
        <br />
        <p>L5 152 Elizabeth Street, Melbourne, VIC 3000.</p>
        <br />
        <p><b>Direct e-mail enquiries to hello@flexdapps.com</b></p>
        <br />
        <p>&copy; Flex Dapps</p>
      </div>
    </div>
  )
}

const About = (props) => {
  return (
    <div className="child goldenrod cc">
      <div>
        <div className="cr">
          <img id="didyouknow" src={Didyouknow}/>
          <h3>Did you know?</h3>
        </div>
        <p className="AboutText">The internet has irreversibly changed people's lives and it only took two decades. We believe the future of computing is <u>decentralized</u>, so we decided to build decentralized applications (DAPPS) on the <i>Ethereum blockchain.</i> It turns out were quite good at it.
        </p>
        <div className="cr blockchain">
          <img className="comp" src={Computer}/>
          <img className="chainlink" src={Link}/>
          <img className="comp" src={Computer}/>
          <img className="chainlink" src={Link}/>
          <img className="comp" src={Computer}/>
          <img className="chainlink" src={Link}/>
          <img className="comp" src={Computer}/>
        </div>
      </div>
    </div>)
}

export { About, Contact, OurWork }



