import React, {Component} from 'react'
import './header.css'
import { Heading } from '../../elements'
import Media from 'react-media'

class Title extends Component {
  render () {
    return (
      <div className='header'>
        <div className="top">
          <img id="etherlogo" className="colorshift" src={require('./etherlogo.png')}/>
        </div>
        <div className="middle">
          <div className="tiltL">
            <img alt="" id="floppydisk" src={require('./floppydisk.png')}/>
          </div>
          <Heading>flex dapps</Heading>
          <div className="tiltR">
            <img alt="" id="floppydisk" src={require('./floppydisk.png')}/>
          </div>
        </div>
        <div className="bottom">
          <img alt="" id="fern" src={require('./fern.png')} />
        </div>
      </div>
    )
  }
}

export default Title



