import React from 'react'
import { imports } from '../../../data/imports'
import { Button } from '../../../elements'
const { Pointer } = imports.appMain

const changeRoute = (history, url) => {
  history.push(url)
  history.location.pathname = url
}

const SwitchButton = ({children, i, updatePointer, route, history, pointer}) => {
  return <Button action={() => {
    changeRoute(history, route)
    updatePointer(i)
  }}>
    {children}
    {i === pointer ? <img src={Pointer} className="pointer"/> : null}
  </Button>
}

export default SwitchButton



