// import react
import React from 'react'
import ReactDOM from 'react-dom'
// import redux things
import { Provider } from 'react-redux'
import { store, history } from './store'

// import componenets
import App from './App'
// import react router deps

ReactDOM.render(
  <Provider store={store}>
    <App history={history} />
  </Provider>,
  document.getElementById('root')
)



