import React, { Component } from 'react'
import { Router, Route } from 'react-router-dom'
import Draggable, {DraggableCore} from 'react-draggable'
import FontAwesome from 'react-fontawesome'

import './appMain.css'
import Media from 'react-media'
import { About, Contact, OurWork } from './content'
import { Window, Handle } from '../../../elements'
import Switch from './switch'
import Mobile from './mobile'

const Narrow = (props) => {
  return (<Window name="Flex 1.2">
    <h1>Welcome to Flex Dapps</h1>
    <div className='mobile'>
      <Mobile />
    </div>
  </Window>)
}

const Wide = (props) => {
  console.log(props.history)
  return (<Window draggable name="Flex 1.2">
    <h1>Welcome to Flex Dapps</h1>
    <div className='app-main cr'>
      <Router history={props.history}>
        <div className='content cr'>
          <Route exact path='/contact' render={(props) => <Contact /> }/>
          <Route exact path='/ourwork' render={(props) => <OurWork /> }/>
          <Route exact path='/' render={(props) => <About />} />
        </div>
      </Router>
      <Switch {...props}/>
    </div>
  </Window>)
}

class AppMain extends Component {
  componentDidMount () {
    this.props.history.push('/')
    this.props.history.location.pathname = '/'
  }

  render () {
    return (
      <Media query="(max-width: 650px)">
        {
          matches => matches
            ? (<Narrow className='test'/>)
            : (<Wide {...this.props} />)
        }
      </Media>
    )
  }
}

export default AppMain



