const defaultState = {
  settings: {
    spriteState: {
      seconds: 0,
      target: 5,
      maxSprites: 4
    },
    appState: null
  }
}

function config (state = defaultState, action) {
  switch (action.type) {
    default:
      return state
  }
}

export default config



