import React, {Component} from 'react'
import '../../App.css'
import { getNewSprite } from '../../services/spriteService'
import { imports } from '../../data/imports'

class Foreground extends Component {
  constructor (props) {
    super(props)
    this.rollForSprite = this.rollForSprite.bind(this)
    this.state = {
      // images: [email, recyclebin, ie, folder]
    }
  }

  rollForSprite () {
    const { settings, sprites } = this.props
    const { seconds, target, maxSprites } = settings.spriteState
    const roll = Math.round(Math.random() * 100)
    const spriteCount = sprites.length
    if (spriteCount < maxSprites) {
      if (roll > target) {
        this.props.createSprite(getNewSprite())
      }
    }
  }

  tick () {
    this.setState(prevState => ({
      seconds: prevState.seconds + 1
    }))
    this.rollForSprite()
  }

  componentDidMount () {
    this.interval = setInterval(() => this.tick(), 1000)
  }

  componentWillUnmount () {
    clearInterval(this.interval)
  }

  render () {
    return (<div className="Foreground">
      {
        this.props.sprites.map((sprite, i) => {
          return (<img
            className={'folder ' + sprite.Direction} src={imports.sprites[sprite.Src]}
            style={{top: sprite.VH + 'VH'}}
            onClick={() => this.props.killSprite(i)}
            key={i}
          />)
        })
      }
    </div>)
  }
}

export default Foreground



