const defaultState = {
  activeButton: 0
}

function pointer (state = defaultState, action) {
  switch (action.type) {
    case 'UPDATE_POINTER':
      return {
        ...state,
        activeButton: action.index
      }
    default:
      return state
  }
}

export default pointer



